@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600&display=swap);
.form{position:relative;width:300px;font-size:18px}.form_field{position:relative;display:flex;align-items:center;height:45px;padding:5px 20px;margin-bottom:20px;border-radius:20px;border:1px solid #dadada;color:#646464}.form_field:focus{border-color:#0dd61c}.form_input{padding:0 10px 0 20px;border:none;font-size:16px;font-weight:600;width:100%}.form_submit{margin-top:10px}.form_error{position:absolute;left:20px;bottom:-20px;white-space:nowrap;text-align:center;color:red;font-size:14px;font-weight:600;-webkit-animation:form_error .25s ease-in;animation:form_error .25s ease-in}@-webkit-keyframes form_error{from{transform:translateX(-10px);opacity:0}to{transform:translateX(0px);opacity:1}}@keyframes form_error{from{transform:translateX(-10px);opacity:0}to{transform:translateX(0px);opacity:1}}input{border:none}input:focus{outline:none}.btn{outline:none;border:none;font-size:18px;font-weight:600;border-radius:50px;height:50px;width:200px;transition:background .25s ease-in-out;cursor:pointer}.btn:active{box-shadow:0 1.5px 3.1px rgba(0,0,0,.035),0 12px 25px rgba(0,0,0,.07)}.btn-success{background:#0dd61c;color:#fff}.btn-success:hover{background:#0ec71a}.btn-info{background:#3ea1c8;color:#fff}.btn-info:hover{background:#2b89ad}.btn-warning{background:#ffb818;color:#fff}.btn-warning:hover{background:#e49f00}.btn-danger{background:red;color:#fff}.btn-danger:hover{background:#e40000}.ant-notification{z-index:999}
#AuthPage{position:relative;display:flex;align-self:center;font-family:"Quicksand";width:900px;height:600px;background:#fff;border-radius:10px;-webkit-animation:AuthPage 1s ease-in-out;animation:AuthPage 1s ease-in-out}#AuthPage_form{display:flex;justify-content:center;align-items:center;flex-direction:column;width:60%;border-radius:10px;background:#fff;transition:transform 1.5s ease}#AuthPage_panel{display:flex;flex-direction:column;justify-content:center;width:40%;height:620px;background:url(../../static/media/AuthPage_panel_bg.b5928a41.png);background-size:cover;background-position:center;border-radius:10px;transition:transform 1.5s ease;transform:translate(0, -10px)}#AuthPage_panel span:first-child{font-size:34px;font-weight:bold;color:#fff}#AuthPage_panel_header{margin-bottom:20px;transition:opacity .25s}#AuthPage_panel_button{transition:opacity .25s}#AuthPage_header{font-size:24px;font-weight:bold;margin-bottom:20px}#AuthPage_forgot{margin-top:10px;cursor:pointer}#AuthPage_forgot:hover{text-decoration:underline}#AuthPage_resend{margin-bottom:10px;cursor:pointer}#AuthPage_resend:hover{text-decoration:underline}#AuthPage_bg{pointer-events:none;left:-170px;bottom:0;position:absolute;-webkit-animation:AuthPage_bg .75s ease-in-out .25s backwards;animation:AuthPage_bg .75s ease-in-out .25s backwards;transition:all .75s ease-in-out}#AuthRegisterVerify{-webkit-animation:fade .5s ease-in-out;animation:fade .5s ease-in-out}#AuthForgotVerify{-webkit-animation:fade .5s ease-in-out;animation:fade .5s ease-in-out}#AuthForgotChange{-webkit-animation:fade .5s ease-in-out;animation:fade .5s ease-in-out}.moveRight{transform:translateX(360px)}.moveLeft{transform:translate(-540px, -10px) !important}.bgMoveLeft{transform:translate(-60px, 10px)}@-webkit-keyframes AuthPage{from{opacity:0;background:transparent}to{opacity:1;background:#fff}}@keyframes AuthPage{from{opacity:0;background:transparent}to{opacity:1;background:#fff}}@-webkit-keyframes AuthPage_bg{from{transform:translateX(50px);opacity:0}to{transform:translateX(0);opacity:1}}@keyframes AuthPage_bg{from{transform:translateX(50px);opacity:0}to{transform:translateX(0);opacity:1}}@-webkit-keyframes fade{from{opacity:0}to{opacity:1}}@keyframes fade{from{opacity:0}to{opacity:1}}
.App{width:100%;height:100vh;text-align:center;display:flex;justify-content:center}.App ::selection{background:#d3d3d3;color:#222}
body {
  background: transparent;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

